#page-numbers {
    list-style: none;
    display: -ms-flexbox;
    display: flex;
    -ms-flex-pack: center;
        justify-content: center;
    -ms-flex-wrap: wrap;
        flex-wrap: wrap;
width: 700px;
margin: auto;
}

li[class*='myid']{
    color: black;
    font-weight: bolder;
    text-decoration: underline;
}


.pagination-number {
    margin-top: 10px; 
    font-size: 25px;
    
}

#page-numbers > li {
    margin-right: 1em;
    color: rgb(120, 109, 109);
    -webkit-user-select: none;
       -moz-user-select: none;
        -ms-user-select: none;
            user-select: none;
    cursor: pointer;
}

.pagination-navigation:hover {
    opacity: 1; 
}

.pagination-navigation {
    color: black; 
    font-size: 40px;
    margin-right: 18px; 
    margin-left: 5px;  
    opacity: 0.5; 
    -webkit-transition: 0.3s; 
    -o-transition: 0.3s; 
    transition: 0.3s;
    cursor: pointer;   
}

.pagination-navigation:hover {
    opacity: 1; 
}




